import React, {Component} from 'react';

import SoundBank from '../components/soundBank';
import Sounds from '../sounds/sounds';
import ReactHowler from 'react-howler';

class SoundBankContainer extends Component{
  constructor(props){
    super(props)
    let sounds = Sounds();
    console.log(sounds);
    this.state = {
      sounds: sounds.tree,
      head: sounds.head,
      step: null,
      playing: false,
      filePath: "",
      playTimer: null
    }

    this.stepInto = this.stepInto.bind(this);
    this.stepUp = this.stepUp.bind(this);
    this.soundClicked = this.soundClicked.bind(this);
    this.playSound = this.playSound.bind(this);
    this.stopSound = this.stopSound.bind(this);
    this.toggleSound = this.toggleSound.bind(this);
  }

  componentWillUnmount(){
    const {playTimer} = this.state;
    if(playTimer !== null){
      clearTimeout(playTimer);
    }
  }

  stepInto(step){
    this.setState({
      step:step
    });
  }

  stepUp(step){
    if(typeof step === 'undefined'){return;}
    this.setState({
      step:step.parent
    });
  }

  soundClicked(item){
    if(this.state.filePath === item.path){
      return this.toggleSound();
    }
    if(this.state.playTimer === null){
      const playTimer = setTimeout(()=>{
        this.setState({playTimer:null});
      },1000);
      this.setState({
        filePath:item.path,
        playing: true,
        playTimer: playTimer
      });
    }
  }

  playSound(){
    this.setState({
      playing: true
    });
  }

  stopSound(){
    this.setState({
      playing:false
    });
  }

  toggleSound(){
    const _playing = !this.state.playing;
    if(_playing){
      this.player.seek(0);
    }
    this.setState({
      playing: _playing
    });
  }

  render(){
    const {sounds, head, step, playing, filePath} = this.state;
    return (
      <div>
        <SoundBank
          sounds={sounds}
          playing={playing}
          head={head}
          step={step}
          filePath={filePath}
          soundClicked={this.soundClicked}
          stopSound={this.stopSound}
          stepInto={(step)=>this.stepInto(step)}
          stepUp={step=>this.stepUp(step)}/>
        <ReactHowler
          src={"./sounds/"+filePath}
          playing={playing}
          loop={false}
          html5={true}
          preload={false}
          onEnd={this.stopSound}
          ref={(ref)=> (this.player = ref)}/>
      </div>
    )
  }
}

export default SoundBankContainer;



