import React, {Component} from 'react';
import SoundBankDragTarget from './dragdrop/SoundBankDragTarget';

class SoundBank extends Component{
  constructor(props){
    super(props);
  }

  render(){
    const {sounds, playing, head, step, stepInto, stepUp, filePath} = this.props;
    const breadcrumbs = () =>{
      if(step === null){
        return (
          <li>Menu</li>
        )
      }
      else{
        const path = buildHeaderBreadcrumbs(step);
        let crumbArray = [];
        crumbArray.push(<li onClick={()=>stepInto(null)} key="menu">Menu</li>)
        crumbArray = crumbArray.concat(path);
        crumbArray.push(<li key={sounds[step].name}>{sounds[step].name}</li>)
        return crumbArray;
      }
    }

    function buildHeaderBreadcrumbs(location, path = []){
      const parent = sounds[location].parent;
      let newPath = path;
      if(parent !== null){
        newPath.unshift(<li onClick={()=>stepInto(parent)} key={parent}>{sounds[parent].name}</li>)
        buildHeaderBreadcrumbs(parent,newPath);
      }
      return newPath;
    }

    const content = () =>{
      let folderArray = [];
      let fileArray = [];

      buildFolders = buildFolders.bind(this);
      buildFiles = buildFiles.bind(this);
      folderStructure = folderStructure.bind(this);
      fileStructure = fileStructure.bind(this);

      buildFolders();
      buildFiles();

      function buildFolders(){
        if(step === null){
          //folderArray.push(folderStructure("Menu", "menu"));
          folderArray.push(folderLabel("Menu"));
          head.forEach((item)=>{
            folderArray.push(folderStructure(sounds[item].name,item));
          });
          return;
        }
        const parentStep = sounds[step].parent;
        const parentName = sounds[parentStep] === null || typeof sounds[parentStep] === 'undefined'? "Menu" : sounds[parentStep].name;
        folderArray.push(folderStructure(parentName,parentStep))
        folderArray.push(folderLabel(sounds[step].name));
        sounds[step].children.forEach((item)=>{
          folderArray.push(folderStructure(sounds[item].name,item));
        });
      }

      function folderStructure(name, item){
        const icon = folderArray.length === 0 ? "fas fa-chevron-up icon" : "far fa-folder icon";
        const click = folderArray.length === 0 ? ()=>stepUp(sounds[step]) : ()=>stepInto(item);
        return(
          <li key={item} onClick={click}>
            <p>
              <i className={icon}></i>
              {name}
            </p>
          </li>
        )
      }

      function folderLabel(name){
        return(
          <li key={name} className="label">
            <p>
              <i className="far fa-folder-open icon"></i>
              {name}
            </p>
          </li>
        )
      }

      function buildFiles(){
        if(step === null){return;}
        if(typeof sounds[step].sounds !== 'undefined'){
          sounds[step].sounds.forEach((file)=>{
            fileArray.push(fileStructure(sounds[file]))
          });
        }
      }

      function fileStructure(file){
        const divClass = file.path === filePath && playing? "selected" : "";
        return(
          <li key={file.path}>
            <SoundBankDragTarget file={file} classes={divClass}>
              <p onClick={()=>this.props.soundClicked(file)}><i className="fas fa-volume-off icon"></i>{file.name}</p>
            </SoundBankDragTarget>
          </li>
        )
      }

      return (
        <div className="sound-bank-content">
          <ul className="folder-structure no-style">
            {folderArray}
          </ul>
          <div className="break"></div>
          <ul className="file-list no-style">
            {fileArray}
          </ul>
        </div>
      )
    }

    return(
      // <div className="col-12 dark sound-repo">
      //   <div className="header row">
      //     <div className="col-12">
      //     <p onClick={this.props.stopSound}>Stop</p>
      //       {header()}
      //     </div>
      //   </div>
      //   {body()}
      // </div>
      <div className="sound-bank dark">
        <div className="sound-bank-head">
          <h4>Soundscape Builder</h4>
        </div>
        <div className="sound-bank-breadcrumbs">
          <ul className="no-style">
            {breadcrumbs()}
          </ul>
        </div>
        {content()}
      </div>
    )
  }
}

export default SoundBank;



